import React from 'react';
import { useState, useEffect } from 'react';
import {
    Panel, PanelHeader, PanelHeaderBack,
    FixedLayout, Tabs, TabsItem, Div, Spinner,
    CellButton, PullToRefresh
} from '@vkontakte/vkui';
import Icon16Fire from '@vkontakte/icons/dist/16/fire';
import bridge from '@vkontakte/vk-bridge';
import axios from 'axios';


import styles from "../styles/custom-styles.sass";
import { ListUsers, ListGroups } from '../components';
import requestMethod from '../helpers/requestMethod';
import { getRankByRating } from '../helpers/getRankByRating';

const Top = ({ ranks, go, id, userPlace, rating, goHome, 
    vk, networkStatus, updateConnection, snackbar, openAlertPay }) => {
    const [userToken, setUserToken] = useState(null);
    const [activeTab, setActiveTab] = useState('general');
    const [friendsTop, setFriendsTop] = useState(null);
    const [renderFriends, setRenderFriends] = useState([]);
    const [fetching, setFetching] = useState(false);
    const [renderGlobal, setRenderGlobal] = useState(0);
    const [renderGroups, setRenderGroups] = useState(0);
    let groupsTop = [];
    let targetIdGroup = 0;


    useEffect(() => {
        getUsers();
        getGroups();
    }, []);




	function addGroupTop(e) {
        targetIdGroup = e.currentTarget.id;
		let urlGroup = groupsTop[targetIdGroup - 1].id;
		console.log("urlGroup", urlGroup);
		bridge.sendPromise('VKWebAppJoinGroup', {
			'group_id': urlGroup
		}).then(res => {
			if (res.result) {
				openAlertPay('Вы уже подписаны на эту группу', 'green');
			}

			console.log("res", res.result);
		}).catch(err => {
			if (err.error_data.error_reason === "User can not join this group") {
				openAlertPay('Подайте заявку, чтобы вступить в группу', 'red');
			}
			console.log("err", err);
		});

	}

    function tabGeneral() {
        setActiveTab('general');
        window.scrollTo(0, 0);
    }

    function tabFriends() {
        setActiveTab('friends');
        getFriendsAccess();
        window.scrollTo(0, 0);
    }

    function tabGroups() {
        setActiveTab('groups');
        window.scrollTo(0, 0);
    }
    
    function getUsers() {
        axios.post(`${requestMethod}/getUsersTop`, {
            search: document.location.search
        }).then(({ data: { status, message } }) => {
            if (status === 'success') {
                setRenderGlobal(message.map((item, index) => {
                    return (
                        <ListUsers
                            key={index}
                            id={index + 1}
                            idVk={item.id}
                            photoURL={item.photoURL}
                            firstName={item.firstName}
                            lastName={item.lastName}
                            rating={item.rating}
                            amount={item.durkaCoins}
                            ranks={ranks || []}
                        />
                    );
                }));
            }

            setFetching(false);
        });
    }

    function getGroups() {
        axios.post(`${requestMethod}/getGroupsTop`, {
            search: document.location.search
        }).then(({ data: { status, message } }) => {
            console.log({ message, status });
            if (status === 'success') {
                groupsTop = message;
                console.log('setGroupsTop', groupsTop);
                setRenderGroups(message.map((item, index) => {
                    return (
                        <ListGroups
                            key={index}
                            id={index + 1}
                            idVk={item.id}
                            photoURL={item.photoURL}
                            name={item.name}
                            rating={item.rating}
                            addGroupTop={addGroupTop}
                        />
                    );
                }));
            }

            setFetching(false);
        });
    }

    function onRefresh() {
        // ВЫЗВАТЬ ЗАПРОС ДАННЫХ ДЛЯ ОТДЕЛЬНОГО ТОПА , ОТОШЕЛ НАЛИТЬ ЧАЯ
        setFetching(true);
        switch (activeTab) {
            case 'general':
                getUsers();
                break;
            case 'friends':
                getFriendsAccess();
                break;
            case 'groups':
                getGroups();
                break;
            default:
                break;
        }
    }

    function getFriendsAccess() {
        bridge.sendPromise('VKWebAppGetAuthToken', {
            "scope": "friends",
            "app_id": 7351258
        }).then(res => {
            if (res.access_token) {
                setUserToken(res.access_token);
                getAndSendToServerFriends(res.access_token);
            } else {
                // TODO: Сделать ошибку о том, что пользователь не предоставил доступ к друзьям
            }
        }).catch(err => {
            vk.log(err);
        });
    }

    function getAndSendToServerFriends(userToken) {
        bridge.sendPromise('VKWebAppCallAPIMethod', {
            "method": "friends.get",
            "params": {
                "access_token": userToken,
                "v": "5.103"
            }
        }).then(res => {
            if (res.response) {
                axios.post(`${requestMethod}/getFriendsTop`, {
                    items: res.response.items,
                    search: document.location.search
                }).then(({ data: { message } }) => {
                    if (!message.length) {
                        return setFriendsTop('У Вас нет друзей в игре');
                    }

                    setFriendsTop(true);
                    setFetching(false);
                    setRenderFriends(message.map((item, index) => {
                        return (
                            <ListUsers
                                key={index}
                                id={index + 1}
                                idVk={item.id}
                                photoURL={item.photoURL}
                                firstName={item.firstName}
                                lastName={item.lastName}
                                rating={item.rating}
                                amount={item.durkaCoins}
                                ranks={ranks || []}
                            />
                        );
                    }));
                });
            } else {
                return setFriendsTop('Ошибка при загрузке друзей');
            }
        }).catch(err => {
            return setFriendsTop('Ошибка при загрузке друзей');
        });
    }
    
 

    return (
        <Panel id={id}>
            <PanelHeader left={<PanelHeaderBack onClick={goHome} data-to="home" />}>
                Топ
            </PanelHeader>

            <FixedLayout vertical="top" filled={true}>
                <Tabs >
                    <TabsItem
                        onClick={tabGeneral}
                        selected={activeTab === 'general'}
                    >
                        Общий
                    </TabsItem>
                    <TabsItem
                        onClick={tabFriends}
                        selected={activeTab === 'friends'}
                    >
                        Друзья
                    </TabsItem>
                    <TabsItem
                        onClick={tabGroups}
                        selected={activeTab === 'groups'}
                    >
                        Сообщества
                    </TabsItem>
                </Tabs>
            </FixedLayout>


            {networkStatus
                ?
                <Div className="divSpinner">
                    < Div className="navigatorDiv">
                        Отсутствует интернет - соединение!
                    </Div>
                    <CellButton
                        className="navigator"
                        onClick={updateConnection}
                        align={"center"}
                    >Повторить
                    </CellButton>
                </Div >
                :
                <>
                    {activeTab === 'general'
                        ?
                        <>
                            {renderGlobal.length
                                ?
                                <>

                                    <Div className="headerTop">
                                        Ваше место: {userPlace}
                                        <>
                                            &#12288;{`Ваш Рейтинг:`}
                                            <Icon16Fire className="SUKA" fill="red" />
                                            {rating}
                                        </>
                                    </Div>
                                    <PullToRefresh onRefresh={onRefresh} isFetching={fetching} style={{ marginTop: 10 }}>
                                        <Div>
                                            {renderGlobal}
                                        </Div>
                                    </PullToRefresh>

                                </>
                                :
                                <div style={{ display: 'flex', alignItems: 'center', flexDirection: 'column' }}>
                                    <Spinner size="large" style={{ marginTop: 90 }} />
                                </div>
                            }
                        </>
                        :
                        ''
                    }


                    {
                        activeTab === 'friends'
                            ?
                            <div style={{ marginTop: 40 }}>
                                {friendsTop === null
                                    ?
                                    <div style={{ display: 'flex', alignItems: 'center', flexDirection: 'column' }}>
                                        <Spinner size="large" style={{ marginTop: 90 }} />
                                    </div>
                                    :
                                    (
                                        !renderFriends.length
                                            ?
                                            <Div>
                                                {friendsTop}
                                            </Div>
                                            :
                                            <PullToRefresh onRefresh={onRefresh} isFetching={fetching} style={{ marginTop: 10 }}>
                                                <Div>
                                                    {renderFriends}
                                                </Div>
                                            </PullToRefresh>

                                    )
                                }
                            </div>
                            :
                            ''
                    }


                    {activeTab === 'groups'
                        ?
                        <div style={{ marginTop: 40 }}>
                            {renderGroups.length
                                ?
                                <PullToRefresh onRefresh={onRefresh} isFetching={fetching} style={{ marginTop: 10 }}>
                                    <Div>
                                        {renderGroups}
                                    </Div>
                                </PullToRefresh>

                                :
                                <div style={{ display: 'flex', alignItems: 'center', flexDirection: 'column' }}>
                                    <Spinner size="large" style={{ marginTop: 90 }} />
                                </div>
                            }
                        </div>
                        :
                        ''
                    }
                    {snackbar}
                </>
            }




        </Panel >
    );
};

export { Top };
